    filterTag(value, row) {
      return row.status === value;
    },
    queryAllGathering() {
      axios.post('/api/query-all-gathering', { group_id: this.$props.currentgroup.id })
        .then((res) => {
          if (res.data.status) {
            this.gatherlist = res.data.gathering_list;
            // appendTag();
            console.log(this.gatherlist);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    createGathering() {

    },
