          if (res.data.status) {
            this.gatherlist = res.data.gathering_list;
            // appendTag();
            this.queryAllGathering();
            console.log(res.data.status);
          } else {
            console.log(res.data.status);
          }
        })
        .catch((err) => {
          console.log(err);
        });
      this.gathering.status = '';
    },
  },
